import QQMapWX from "../../utils/qqmap-wx-jssdk.js"
var qqmapsdk = new QQMapWX({
	key:"4J4BZ-UZ2L6-3VFSM-ES6KI-V434E-IQF7C"
})
export default {
	data() {
		return {
			addRessName:"",
			latitude:"",
			longitude:"",
			indexInfo:[],
			indexImg:'',
			area_info:[{'id':0,'name':'全部区域'}],
			index:0,
			area_id:0,
			page:1,
			pagesize:10,
			input_value:"",
			area_name:"全部区域"
		}
	},
	onLoad() {
	},
	onShareAppMessage( options ){
	  　　var that = this;
	  　　// 设置菜单中的转发按钮触发转发事件时的转发内容
	  　　var shareObj = {
	  　　　　title: "雀胜",        // 默认是小程序的名称(可以写slogan等)
	  　　　　path: '/pages/Home/Home',// 默认是当前页面，必须是以‘/’开头的完整路径
	  　　　　imageUrl: '',     //自定义图片路径，可以是本地文件路径、代码包文件路径或者网络图片路径，支持PNG及JPG，不传入 imageUrl 则使用默认截图。显示图片长宽比是 5:4
	  　　　　success: function(res){
	  　　　　　　// 转发成功之后的回调
	  　　　　　　if(res.errMsg == 'shareAppMessage:ok'){
	  　　　　　　}
	  　　　　},
	  　　　　fail: function(){
	  　　　　　　// 转发失败之后的回调
	  　　　　　　if(res.errMsg == 'shareAppMessage:fail cancel'){
	  　　　　　　　　// 用户取消转发
	  　　　　　　}else if(res.errMsg == 'shareAppMessage:fail'){
	  　　　　　　　　// 转发失败，其中 detail message 为详细失败信息
	  　　　　　　}
	  　　　　}
	  　　};
	  　　// 返回shareObj
	  　　return shareObj;
	  },
	onPullDownRefresh() {
	      this.refresh()
	    },
	onReachBottom() {
		this.shangLa()
	},
	onShow() {
		this.dingWei()
	},
	methods: {
	    async postIndex(pages){
			uni.showLoading({})
			const that = this
			const res = await this.$myRequest({
				method:"POST",
				url:"/indexInfo",
				data:{
					lat:that.latitude,
					lng:that.longitude,
					area_id:that.area_id,
					page:pages,
					keys:that.input_value
				}
			})				
			uni.hideLoading()
			uni.stopPullDownRefresh()
			that.indexInfo = res.data.data,
			that.area_info = res.data.area_info
		},
		particulars (id,x,y){
			const token = uni.getStorageSync("token")
			if(!token){
				uni.navigateTo({
					url:"../login/login"
				})
			}else{
				uni.showLoading({})
				uni.navigateTo({
					url:"../hotel_particulars/hotel_particulars?id=" + id +"&x=" + x + "&y=" + y
				})
				uni.hideLoading()
			}
		},
		bindPickerChange: function(e) {
			this.area_id = this.area_info[e.detail.value].id;
			this.area_name = this.area_info[e.detail.value].name;
			this.postIndex(1);
		},
		shangLa(){
			const that = this;
			uni.showLoading({
				title:"正在加载..."
			})
			var pages = that.page+1
			that.page = pages
			that.postIndex(that.page)
		},
		sousuos(event){
			this.input_value = event.target.value
			//this.postIndex(1)
		},
		refresh(){
			setTimeout(()=>{
				if(this.longitude == "" || this.latitude == ""){
				       this.dingWei()	
					   return
				}
				this.postIndex()					
				uni.stopPullDownRefresh()
			},2000)
		},
		dingWei(){
			const that = this
			uni.getLocation({
			    type: 'gcj02',
				geocode:true,
			    success:(res)=>{
					that.longitude = res.longitude,
					that.latitude = res.latitude
					that.postIndex(1)
					qqmapsdk.reverseGeocoder({
							location:{
								latitude:res.latitude,
								longitude:res.longitude,
							},
							success:(res)=>{
								var RES = JSON.parse(JSON.stringify(res))
								that.addRessName = res.result.ad_info.districtarea_info
								//that.postIndex(1)
							},
							fail:function(res){
							}
					})
			    },
				fail:()=>{
					uni.showModal({
			          title: '警告',
			          content: '您点击了拒绝授权,将无法正常地理位置信息,点击确定重新获取授权。并打开手机定位',
			          success: function (res) {
						if (res.confirm) {
						  uni.openSetting({
							success: function (datas) {
							  if (datas.authSetting["scope.userLocation"]) {
								uni.getLocation({
								  success: function (res) {										
									that.longitude = res.longitude,
									that.latitude = res.latitude
									that.postIndex(1)
									qqmapsdk.reverseGeocoder({
										location:{
											latitude:res.latitude,
											longitude:res.longitude,
										},
										success:(res)=>{
											var RES = JSON.parse(JSON.stringify(res))
											that.addRessName = res.result.ad_info.districtarea_info
											//that.postIndex(1)
										},
										fail:function(res){
										}
									})
								  }
								})
							  } 
							  else {
								that.dingWei();
							  }
							}
						  })
						} else {
						  that.dingWei();
						}
					  }
				   })
					
				},
				complete: () => {}
			});
		},
	}
}
